@charset "UTF-8";
/**
 * Light-themed version:
 * uses light theme by default but switches to dark theme
 * if a system-wide theme preference is set on the user's device.
 *
 * Variables will remain uncompiled so the theme can update dynamically
 * at runtime in the browser.
 */
/* To use this file from Sass, import `_variables/_variables-light` */
:root {
  --background: #ffffff;
  --background-alt: #efefef;
  --selection: #9e9e9e;
  --text-main: #363636;
  --text-bright: #000000;
  --links: #0076d1;
  --focus: #0096bfab;
  --border: #dbdbdb;
  --code: #000000;
  --animation-duration: 0.1s;
  --button-hover: #dddddd;
  --scrollbar-thumb: #d5d5d5;
  --scrollbar-thumb-hover: #c4c4c4;
  --form-placeholder: #949494;
  --form-text: #000000;
}

@media (prefers-color-scheme: dark) {
  /* To use this file from Sass, import `_variables/_variables-dark` */
  :root {
    --background: #202b38;
    --background-alt: #161f27;
    --selection: #161f27;
    --text-main: #dbdbdb;
    --text-bright: #ffffff;
    --links: #41adff;
    --focus: #0096bfab;
    --border: #dbdbdb;
    --code: #ffbe85;
    --animation-duration: 0.1s;
    --button-hover: #324759;
    --scrollbar-thumb: #324759;
    --scrollbar-thumb-hover: #415c73;
    --form-placeholder: #a9a9a9;
    --form-text: #ffffff;
  }
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.4;
  max-width: 800px;
  margin: 20px auto;
  padding: 0 10px;
  color: var(--text-main);
  background: var(--background);
  text-rendering: optimizeLegibility;
}

button, input, textarea {
  transition: background-color var(--animation-duration) linear, border-color var(--animation-duration) linear, color var(--animation-duration) linear, box-shadow var(--animation-duration) linear, transform var(--animation-duration) ease;
}

h1 {
  font-size: 2.2em;
  margin-top: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 12px;
}

h1,
h2,
h3,
h4,
h5,
h6,
strong {
  color: var(--text-bright);
}

h1,
h2,
h3,
h4,
h5,
h6,
b,
strong,
th {
  font-weight: 600;
}

blockquote {
  border-left: 4px solid var(--focus);
  margin: 1.5em 0em;
  padding: 0.5em 1em;
  font-style: italic;
}

blockquote > footer {
  margin-top: 10px;
  font-style: normal;
}

blockquote cite {
  font-style: normal;
}

address {
  font-style: normal;
}

a[href^='mailto']::before {
  content: 'ðŸ“§ ';
}

a[href^='tel']::before {
  content: 'ðŸ“ž ';
}

a[href^='sms']::before {
  content: 'ðŸ’¬ ';
}

button,
input[type='submit'],
input[type='button'],
input[type='checkbox'] {
  cursor: pointer;
}

input:not([type='checkbox']):not([type='radio']),
select {
  display: block;
}

input, select, button, textarea {
  color: var(--form-text);
  background-color: var(--background-alt);
  font-family: inherit;
  font-size: inherit;
  margin-right: 6px;
  margin-bottom: 6px;
  padding: 10px;
  border: none;
  border-radius: 6px;
  outline: none;
}

input:not([type='checkbox']):not([type='radio']),
select, button, textarea {
  -webkit-appearance: none;
}

textarea {
  margin-right: 0;
  width: 100%;
  box-sizing: border-box;
  resize: vertical;
}

button, input[type='submit'], input[type='button'] {
  padding-right: 30px;
  padding-left: 30px;
}

button:hover,
input[type='submit']:hover,
input[type='button']:hover {
  background: var(--button-hover);
}

input:focus,
select:focus,
button:focus,
textarea:focus {
  box-shadow: 0 0 0 2px var(--focus);
}

input[type='checkbox']:active,
input[type='radio']:active,
input[type='submit']:active,
input[type='button']:active,
button:active {
  transform: translateY(2px);
}

input:disabled,
select:disabled,
button:disabled,
textarea:disabled {
  cursor: not-allowed;
  opacity: .5;
}

::placeholder {
  color: var(--form-placeholder);
}

a {
  text-decoration: none;
  color: var(--links);
}

a:hover {
  text-decoration: underline;
}

code, kbd {
  background: var(--background-alt);
  color: var(--code);
  padding: 5px;
  border-radius: 6px;
}

pre > code {
  padding: 10px;
  display: block;
  overflow-x: auto;
}

img {
  max-width: 100%;
  height: auto;
}

hr {
  border: none;
  border-top: 1px solid var(--border);
}

table {
  border-collapse: collapse;
  margin-bottom: 10px;
  width: 100%;
}

td, th {
  padding: 6px;
  text-align: left;
}

th {
  border-bottom: 1px solid var(--border);
}

tbody tr:nth-child(even) {
  background-color: var(--background-alt);
}

::-webkit-scrollbar {
  height: 10px;
  width: 10px;
}

::-webkit-scrollbar-track {
  background: var(--background-alt);
  border-radius: 6px;
}

::-webkit-scrollbar-thumb {
  background: var(--scrollbar-thumb);
  border-radius: 6px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--scrollbar-thumb-hover);
}

::selection {
  background-color: var(--selection);
}
